import {Component, EventEmitter, OnInit, Output} from '@angular/core';
import {ProductService} from '../../../shared/services/product.service';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {Product} from '../../../shared/classes/product';
import {ViewportScroller} from '@angular/common';
import {trigger, state, style, animate, transition} from '@angular/animations';

@Component({
  selector: 'app-product-by-category',
  templateUrl: './product-by-category.component.html',
  styleUrls: ['./product-by-category.component.scss'],
    animations: [
        // Each unique animation requires its own trigger. The first argument of the trigger function is the name
        trigger('rotatedState', [
            state('default', style({ transform: 'rotate(0)' })),
            state('rotated', style({ transform: 'rotate(-360deg)' })),
            transition('rotated => default', animate('2000ms ease-out')),
            transition('default => rotated', animate('2000ms ease-in'))
        ])
    ]
})
export class ProductByCategoryComponent implements OnInit {
    products: Product[];
    category: string;
    type: string;

    public brands: any[] = [];
    public colors: any[] = [];
    public size: any[] = [];
    public tags: any[] = [];

    public grid = 'col-xl-3 col-md-6';
    public layoutView = 'grid-view';
    public productCenter = '';

    public pageNo = 1;
    public paginate: any = {};
    public minPrice = 0;
    public maxPrice = 1200;
    public sortBy: any;

    public hover = false;
    public timeOut = null;

    state = 'default';
    @Output() stateEmitter = new EventEmitter<string>();


    constructor(public productService: ProductService, protected route: ActivatedRoute, protected router: Router,
                public viewScroller: ViewportScroller) {
    }

    ngOnInit(): void {
        this.route.queryParams.subscribe((queryParams: Params) => {
            this.category = queryParams.category ? queryParams.category : null;
            this.type = queryParams.type ? queryParams.type : null;
            this.brands = queryParams.brands ? queryParams.brands.split(',') : [];
            this.sortBy = queryParams.sortBy ? queryParams.sortBy : 'ascending';
            this.pageNo = queryParams.page ? queryParams.page : this.pageNo;

            this.brands = queryParams.brand ? queryParams.brand.split(',') : [];
            this.colors = queryParams.color ? queryParams.color.split(',') : [];
            this.size = queryParams.size ? queryParams.size.split(',') : [];
            this.tags = [...this.brands, ...this.colors, ...this.size]; // All Tags Array

            // Pagination use only
            this.productService.filterProducts(this.tags).subscribe(products => {
                if (products.length === 0) { // When product is empty redirect 404
                    this.router.navigateByUrl('/pages/404', {skipLocationChange: true});
                } else {
                    // Sorting Filter
                    this.products = this.productService.sortProducts(products, this.sortBy);

                    // Category Filter
                    if (queryParams.category) {
                        this.products = this.products.filter(item => item.category == this.category);
                    }

                    // type Filter
                    if (queryParams.type) {
                        this.products = this.products.filter(item => item.type === this.type);
                    }

                    // brand filter
                    // this.products.filter((item) => {
                    //     if (this.brands.indexOf(item.brand) === -1) {
                    //         this.brands.push(item.brand);
                    //     }
                    // });

                    // Price Filter
                    this.products = this.products.filter(item => item.price >= this.minPrice && item.price <= this.maxPrice);

                    // Paginate Products
                    this.paginate = this.productService.getPager(this.products.length, +this.pageNo);     // get paginate object from service
                    this.products = this.products.slice(this.paginate.startIndex, this.paginate.endIndex + 1); // get current page of items
                }
            });
        });

        setInterval(() => {
            this.state = (this.state === 'default' ? 'rotated' : 'default');
            this.stateEmitter.emit(this.state);
        }, 6000);
    }

    onHover() {
        if (this.timeOut) {
            window.clearTimeout(this.timeOut);
        }

        this.hover = true;
    }

    onLeave() {
        this.timeOut = setTimeout(() => {
            this.hover = false;
        }, 2000);
    }

    // Append filter value to Url
    updateFilter(tags: any) {
        tags.page = null; // Reset Pagination
        this.router.navigate([], {
            relativeTo: this.route,
            queryParams: tags,
            queryParamsHandling: 'merge', // preserve the existing query queryParams in the route
            skipLocationChange: false  // do trigger navigation
        }).finally(() => {
            this.viewScroller.setOffset([120, 120]);
            this.viewScroller.scrollToAnchor('products'); // Anchore Link
        });
    }

    // Product Tab collection
    getBrandProducts(brand) {
        return this.products.filter((item) => {
            if (item.brand === brand) {
                return item;
            }
        });
    }

    // SortBy Filter
    sortByFilter(value) {
        this.router.navigate([], {
            relativeTo: this.route,
            queryParams: { sortBy: value ? value : null},
            queryParamsHandling: 'merge', // preserve the existing query queryParams in the route
            skipLocationChange: false  // do trigger navigation
        }).finally(() => {
            this.viewScroller.setOffset([120, 120]);
            this.viewScroller.scrollToAnchor('products'); // Anchore Link
        });
    }


    // product Pagination
    setPage(page: number) {
        this.router.navigate([], {
            relativeTo: this.route,
            queryParams: {page},
            queryParamsHandling: 'merge', // preserve the existing queryParams in the route
            skipLocationChange: false  // do trigger navigation
        }).finally(() => {
            this.viewScroller.setOffset([120, 120]);
            this.viewScroller.scrollToAnchor('products'); // Anchore Link
        });
    }

    // Change Grid Layout
    updateGridLayout(value: string) {
        this.grid = value;
    }

    // Change Layout View
    updateLayoutView(value: string) {
        this.layoutView = value;
        if (value == 'list-view') {
            this.grid = 'col-lg-12';
            this.productCenter = '';
        }
        else {
            this.grid = 'col-xl-3 col-md-6';
        }
    }

    // customizing product center
    updateProductLayout(value: string) {
        this.layoutView = 'list-view';
        this.grid = 'col-lg-12';
        this.productCenter = value;
    }

}
